.loginsignup{
    width: 100%;
    height: 80vh;
    background: #fce4fe;
    padding-top: 30px;
    padding-bottom:70px ;
    margin-bottom: 20px;
}

.loginsignup-container{
    width: 580px;
    height: 500px;
    background:white;
    margin: auto;
    padding: 40px 60px;
}

.loginsignup-container h1{
    margin: 20px 0px;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    align-items: center;
    margin-top: 25px;
}

.loginsignup-fields input{
    height: 40px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color:#5c5c5c;
    font-size: 18px;
}
.loginsignup-container button{
    width: 84%;
    height: 42px;
    background: #ff1414f6;
    color: #fff;
    border: none;
    margin-top: 11px;
    margin-bottom: 12px;
    font-size: 22px;
    font-weight: 500;
    cursor: pointer;
}

.loginsignup-login{
    margin: 10px auto;
    color:#5c5c5c;
    font-size: 18px;
    font-weight: 500;
    padding-left: 100px;
}

.loginsignup-login span{

    color:#ff4141;
    font-weight: 600;
    cursor: pointer;
}
.loginsignup-agree{
    display: flex;
    align-items: center;
    margin-top: 15px;
    gap:20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
