.descriptionbox{
    margin: 120px 170px;

}
.descriptionbox-navigator{
    display: flex;
}
.descriptionbox-nav-box{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
    height: 70px;
    width: 171px;
    border: 1px solid #d0d0d0;
}

.descriptionbox-nav-box .fade{
    color: #555;
    background: #FBFBFB;
}

.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #d0d0d0;
    padding: 48px;
    padding-bottom:70px ;
}
