.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding:40px 180px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff 10%,#e1ffea22 60%);
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color:#171717;
     font-size: 75px;
     font-weight: 600;


}
.offers-left p{
    color:#171717;
    font-size: 20px;
    font-weight: 600;
}

.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 75px;
    background: #ff1414;
    border: none;
    color:white;
    font-size:22px;
    font-weight: 500;
    margin-top: 30px;
    cursor:pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
