.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
    height: 90vh;
}
.popular h1{
    color:#171717;
    font-weight: 600;
    font-size: 50px;
}
.popular hr{
    width: 200px;
    height: 10px;
    border-radius: 10px;
    background: #252525;
}

.popular-items{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}